import { Component, OnInit } from '@angular/core';
import { CmsService } from '../../services/cms.service';

@Component({
  selector: 'app-faqs-category',
  templateUrl: './faqs-category.component.html',
  styleUrls: ['./faqs-category.component.scss']
})
export class FaqsCategoryComponent implements OnInit {

  public faqcategoryList: any;
  public faqList: any;
  public ls: any[];
  public list: any[] = [];

  constructor(
    private faqService: CmsService,
    private faqCategoryService: CmsService,
    private faqSubCategoryService: CmsService
  ) { }

  ngOnInit() {
    this.getFaqCategory();
  }

  getFaqCategory() {
    this.faqSubCategoryService.getFaqSubCategory()
      .subscribe(res => {
        let j = 0;
        for (const prop of res) {
          if (this.list[prop.faqcategoryid] === undefined) {
            this.list[prop.faqcategoryid] = [];
            // console.log(this.list);
          }
          this.list[prop.faqcategoryid].push(prop);
          console.log(prop);
          if (j === 0) {
            this.getFaqByCategory(prop.id);
          }
          j++;
        }
      });
    this.faqCategoryService.getFaqCategory()
      .subscribe(res => {
        this.faqcategoryList = res;
      });
  }

  getFaqByCategory(faqcategoryid: string) {
    this.faqService.getFaqBySubcategoryId(faqcategoryid)
      .subscribe(res => {
        this.faqList = res;
      });
  }
}
